import omit from 'lodash/omit'
import { useState } from 'react'
import { useSelector } from 'react-redux'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import styles from './styles.module.css'
import { selectLayers } from '../Layers/layersSlice'

function MultiSelect ({ selected, options, onUpdate }) {
  function handleChange (index) {
    onUpdate(
      selected.includes(index)
        ? selected.filter(i => i !== index)
        : [...selected, index]
    )
  }
  return (
    <ul className={styles.layerList}>
      {options.map((option, i) => (
        <li
          key={i}
          data-selected={selected.includes(i)}
          onClick={() => handleChange(i)}
        >
          {option}
        </li>
      ))}
    </ul>
  )
}

function ConfigModal ({ combo, onUpdate }) {
  const layers = useSelector(selectLayers)
  const [pending, setPending] = useState(combo)

  const availableLayers = layers.map(layer => layer.name)
  function handleConfirm () {
    onUpdate(pending)
  }

  return (
    <Modal>
      <DialogBox onDismiss={handleConfirm}>
        <h2>Customize [<code>{combo.name}</code>]</h2>
        <div>

          <div className={styles.field}>
            <label htmlFor="customize-combo-timeout">
              Timeout (ms)
            </label>
            <input
              id="customize-combo-timeout"
              type="number"
              placeholder="Default"
              value={pending.properties['timeout-ms']?.parsed || ''}
              onChange={event => {
                const parsed = Number(event.target.value)
                setPending(state => ({
                  ...state,
                  properties: event.target.value !== ''
                    ? { ...state.properties, 'timeout-ms': { parsed } }
                    : { ...omit(state.properties, 'timeout-ms') }
                }))
              }}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="customize-combo-slow-release">
              Slow Release
            </label>
            <input
              id="customize-combo-slow-release"
              type="checkbox"
              checked={pending.properties['slow-release']?.parsed || false}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  properties: {
                    ...state.properties,
                    'slow-release': {
                      parsed: event.target.checked
                    }
                  }
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label>Layers</label>
            <MultiSelect
              selected={pending.properties.layers?.parsed || []}
              options={availableLayers}
              onUpdate={layers => (
                setPending(state => ({
                  ...state,
                  properties: {
                    ...state.properties,
                    layers: {
                      parsed: layers
                    }
                  }
                }))
              )}
            />
          </div>
        </div>
      </DialogBox>
    </Modal>
  )
}

export default ConfigModal
